@Component({
  selector: "app-home",
  templateUrl: "./home.component.html",
  styleUrls: ["./home.component.css"]
})
export class HomeComponent implements OnInit {
  itemCount: number = 0;
  placeholder = "give id of repair product";
  items_column: Array<string> = [null, null, null, null, null];
  //items: Array<Array<string>> = [];
  items = new BehaviorSubject<Array<any>>([]);
  items_observable = this.items.asObservable();
  dummy_items: Array<string> = [];
  quantity_index: Array<number> = [];
  price_list = [];
  constructor() {}

  ngOnInit() {
    for (var i = 0; i < 3; i++) {
      var array: Array<Array<string>> = this.items.getValue();
      array.push([null, null, null, null, null]);
      this.items.next(array);
    }
    //this.price_list = price_list;
  }
  billOnChange() {
    var a = [];
    this.items.getValue().forEach(element => {
      a.push(element);
    });
    this.items.next(a);
  }
}
